import React, { FC, useEffect, useState } from 'react';
import { SupNavBar, SupNavBarProps } from '@sup-mobile/ui';
import { JSBridge } from '@sup-mobile/framework';
import { Tabs, WhiteSpace, Badge, List, Icon, Accordion } from 'antd-mobile';
import { getEmerStaffData } from '../../../api/requestConfig'
import icon from '../../../assets/img/person.png';
import gangwei from '../../../assets/img/gangwei.png';
import phone from '../../../assets/img/phone1.png';

import './index.less';
import { GetRequest } from '../../../util/common';

const ActionOwner: FC<any> = (props: any) => {

  let requestParams = GetRequest(window.location.href);

  const [list, setList] = useState<any>({})

  // const data = [
  //   [{
  //     name: 'wcz',
  //     station: 'SESEAB_Setionmember_role/001',
  //     phone: '13838895467'
  //   },
  //   {
  //     name: '王永强',
  //     station: 'SESEAB_Setionmember_role/002',
  //     phone: '13838895468'
  //   }],
  //   [{
  //     name: '王巧',
  //     station: 'SESEAB_Setionmember_role/001',
  //     phone: '19157875465'
  //   },
  //   {
  //     name: '王永强',
  //     station: 'SESEAB_Setionmember_role/002',
  //     phone: '13838895468'
  //   },
  //   {
  //     name: '李大洋',
  //     station: 'SESEAB_Setionmember_role/003',
  //     phone: '19157875464'
  //   }]
  // ];

  useEffect(() => {
    initData();
  }, [])

  const initData = async () => {
    const data = await getEmerStaffData(requestParams.id)
    const dataMap = (data || []).reduce((result: { [x: string]: any[]; }, obj: { sectionName: any; }) => {
      const category = obj.sectionName;
      if (!result[category]) {
        result[category] = [];
      }
      result[category].push(obj);
      return result;
    }, {});
    setList(dataMap)
  }

  const fillProps: SupNavBarProps = {
    mode: 'fill',
    onLeftClick: () => {
      window.location.href = `#/demo/eventEditing?id=${requestParams.id}&tabIndex=${requestParams.tabIndex}`

    },
  }

  const ListItem = (data: any) => {
    
    return <>
      {
        data.map((item: any) => (
          <div className='list-content'>
            <p className='topBg' />
            <div className='content'>
              <img className='img' src={icon} />
              <span>名字</span>
              <span className='value'>{item.name || '-'}</span>
            </div>
            <div className='content'>
              <img className='img' src={gangwei} />
              <span>岗位</span>
              <span className='value'>{item.role || '-'}</span>
            </div>
            <div className='content'>
              <img className='img' src={phone} />
              <span>报警人电话</span>
              <span className='value'>{item.mobile || '-'}</span>
            </div>
          </div>
        ))
      }
    </>
  }


  return (
    <div className='actionOwner'>
      <SupNavBar  {...fillProps}>行动负责人</SupNavBar>
      <div className='actionOwner-content'>
        <Accordion defaultActiveKey="0" className="actionOwner-accordion" onChange={() => { }}>
          {Object.keys(list).length ? Object.values(list).map((item,index) => {
            return <Accordion.Panel header={<div>
              <span style={{ borderLeft: '0.8vw solid #328af7', paddingLeft: '2vw', fontWeight: '700' }}>{Object.keys(list)[index]}</span>
            </div>}>
              <List className="actionOwner-list">
                <List.Item>
                  {
                    ListItem(item)
                  }
                </List.Item>
              </List>
            </Accordion.Panel>
          }) : <div style={{ margin: '30px 100px', textAlign: 'center' }}>暂无数据</div>}
        </Accordion>
      </div>
    </div>
  );
};

export default ActionOwner